@import url(https://css.gg/spinner-alt.css);
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  width: 100vw;
}
.header{
    background-image: url(/pizzavilag/static/media/header.f34d0ef1.jpg);
    background-position: center center;
    background-size: cover;
    width: 50%;
    border-radius: 15px 50px;
    text-shadow: 0px 2px 4px black;
    position: relative;
    max-width: 800px;
}

.i{
    position: absolute;
    right: 10%;
    cursor: context-menu;
    font-size: 3vw;
}

.cms{
    cursor: context-menu;
}
nav{
    height: 100px;
}
.footer
{
    margin-top: 10px;
    width: 100%;
    height: auto;
}
.App {
  text-align: center;
}
@-webkit-keyframes spinneralt {
  0% { transform: rotate(0deg) }
  to { transform: rotate(359deg) }
 }
@keyframes spinneralt {
  0% { transform: rotate(0deg) }
  to { transform: rotate(359deg) }
 }
 .gg-spinner-alt {
  transform: scale(1);
  transform: scale(var(--ggs,1))
 }
 .gg-spinner-alt,
 .gg-spinner-alt::before {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 120px;
  height: 120px
 }
 .gg-spinner-alt::before {
  content: "";
  position: absolute;
  border-radius: 100px;
  -webkit-animation: spinneralt 1s cubic-bezier(.6,0,.4,1) infinite;
          animation: spinneralt 1s cubic-bezier(.6,0,.4,1) infinite;
  border: 3px solid transparent;
  border-top-color: currentColor
 } 
.appBody
 {
    background-color: #FAD784;
    height: auto;
    min-height: 100vh;
    width: auto;
    min-width: 100vw;
    font-style: normal;
    font-weight: normal;
 }

